.InnerHeader {
  position: relative;
  width: 89.5%;
  height: 7%;
  min-height: 46px;
  background-color: white;
  /* border: 2px solid blue; */
  box-shadow: 0 0 10px 1px #777;
  z-index: 3;
  padding-left: 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.fillterApplication {
  width: 20%;
  height: 100%;
  /* border: 2px solid red; */
  font-size: 1rem;
  font-weight: 900;
  vertical-align: middle;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  transition: all 0.3s ease-in-out;
}
.fillterApplication > p {
  font-weight: 700;
  margin-left: 10px;
}
.fillterApplication > img {
  width: 22px;
  cursor: pointer;
}
.ApplicationSecurity {
  width: 50%;
  height: 100%;
  /* border: 2px solid red; */
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
.ApplicationStatus {
  width: 80%;
  height: 100%;
  padding: 20px 10px 20px 0px;
}
.ApplicationStatusInnerDiv {
  width: 100%;
  height: 100%;
  border-left: 1px solid #3c3c3c;
  padding-left: 20px;
  font-size: 1rem;
  font-weight: 500;
  display: flex;
  align-items: center;
  justify-content: space-around;
  color: #585858;
}
.ApplicationStatusInnerDiv > p > b {
  color: #ed0295;
}
.Security {
  width: 188px;
  height: 100%;
  background-color: #3c3c3c;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1rem;
  font-weight: 700;
  color: white;
  transition: all 0.3s ease-in-out;
}
.greenDot {
  margin-left: 10px;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background-color: #95dd46;
}
@media screen and (max-width: 1900px) {
  .Security {
    font-size: 0.7rem;
  }
  .ApplicationStatusInnerDiv {
    font-size: 0.7rem;
  }
}

@media screen and (max-width: 1024px) {
  .ApplicationStatusInnerDiv {
    display: none;
  }
  .Security {
    height: 46px;
  }
  .fillterApplication{
    width: 40%;
  }
  .fillterApplication > img {
    width: 16px;
  }
  .InnerHeader {
    width: 90%;
    margin-bottom: 15px;
  }
}
@media screen and (max-width:800px) {
.fillterApplication > p {
  font-size: 0.6rem;
}
.Security {
 font-size: 0.5rem;
}
}